  formTabs();

  const scrollTo = () => {
    document.body.addEventListener('click', (e) => {
      if (e.target.closest('a[href^="#"]')) {
        e.preventDefault();
        const target = e.target.closest('a[href^="#"]').getAttribute('href');
        document.querySelector(target).scrollIntoView({
          behavior: 'smooth',
          block: 'start',
        });
      }
    });
  };

